// a component that displays the raw messages in the chat

'use client';

import { RawMessage } from '@/types/chat';
import { cn } from '@/lib/utils';
import { motion } from 'framer-motion';
import { Bug, ChevronRight } from 'lucide-react';
import { useMemo, useRef, useEffect } from 'react';

interface DebugPanelProps {
  messages: RawMessage[];
}

interface Metadata {
  thread_id?: string;
  langgraph_node?: string;
  ls_model_name?: string;
  ls_temperature?: number;
  [key: string]: any;
}

interface MessageData {
  type?: string;
  content?: string;
  [key: string]: any;
}

const messageVariants = {
  hidden: { opacity: 0, x: 20 },
  visible: { opacity: 1, x: 0 },
  exit: { opacity: 0, x: 20 }
};

function consolidateStreamingContent(messages: RawMessage[]) {
  const consolidated: RawMessage[] = [];
  let currentContent = '';
  let currentMetadata: Metadata | null = null;
  let currentThreadId: string | null = null;
  let lastEventType = '';
  let currentState: any = null;

  // Helper function to normalize content
  const normalizeContent = (content: string) => {
    return content.replace(/\s+/g, ' ').trim();
  };

  // Helper function to flush current content
  const flushCurrentContent = () => {
    if (currentContent || currentState) {
      consolidated.push({
        event: 'stream_state',
        data: {
          content: currentContent ? normalizeContent(currentContent) : '',
          metadata: currentMetadata,
          state: currentState,
          timestamp: new Date().toISOString()
        }
      });
      currentContent = '';
      currentState = null;
    }
  };

  messages.forEach((msg) => {
    if (msg.event === 'metadata') {
      const newThreadId = (msg.data as Metadata)?.thread_id;
      if (newThreadId !== currentThreadId) {
        flushCurrentContent();
        
        if (consolidated.length > 0) {
          consolidated.push({
            event: 'separator',
            data: { timestamp: new Date().toISOString() }
          });
        }
        currentThreadId = newThreadId || null;
      }
      consolidated.push(msg);
      lastEventType = 'metadata';
    } else if (msg.event === 'messages') {
      const messageData = msg.data[0] as MessageData;
      const metadata = msg.data[1] as Metadata;
      const threadId = metadata?.thread_id;
      
      if (messageData?.type === 'AIMessageChunk') {
        if (lastEventType !== 'streaming' || threadId !== currentThreadId) {
          flushCurrentContent();
          currentContent = messageData.content || '';
          currentMetadata = metadata;
        } else {
          const newContent = messageData.content || '';
          if (newContent) {
            currentContent += (currentContent && !currentContent.endsWith(' ') ? ' ' : '') + newContent;
          }
          currentMetadata = metadata;
        }
        lastEventType = 'streaming';
        currentThreadId = threadId || null;
      } else {
        flushCurrentContent();
        consolidated.push({
          event: 'messages',
          data: [
            { ...messageData, content: messageData.content ? normalizeContent(messageData.content) : '' },
            metadata
          ]
        });
        lastEventType = 'message';
      }
    } else if (msg.event === 'updates') {
      // Handle state updates
      currentState = msg.data;
      consolidated.push({
        event: 'state_update',
        data: {
          state: msg.data,
          timestamp: new Date().toISOString()
        }
      });
      lastEventType = 'state';
    } else if (msg.event === 'events') {
      // Handle events
      consolidated.push({
        event: 'event',
        data: {
          event: msg.data,
          timestamp: new Date().toISOString()
        }
      });
      lastEventType = 'event';
    } else {
      // Handle any other events
      consolidated.push({
        event: msg.event,
        data: {
          content: msg.data,
          timestamp: new Date().toISOString()
        }
      });
      lastEventType = 'other';
    }
  });

  flushCurrentContent();
  return consolidated;
}

export function DebugPanel({ messages }: DebugPanelProps) {
  const consolidatedMessages = useMemo(() => consolidateStreamingContent(messages), [messages]);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [consolidatedMessages]);

  const renderEventData = (msg: RawMessage) => {
    if (msg.event === 'stream_state') {
      return (
        <div className="space-y-3">
          <div className="space-y-2">
            <div className="text-yellow-300/90 text-sm font-medium">Streaming State</div>
            <div className="text-gray-300 text-sm break-words pl-2 space-y-2">
              {msg.data.content && (
                <div>
                  <span className="text-blue-300">Content:</span> {msg.data.content}
                </div>
              )}
              {msg.data.state && (
                <div>
                  <span className="text-blue-300">State:</span>
                  <pre className="text-xs bg-[#262626]/30 p-2 rounded mt-1 font-mono overflow-x-auto">
                    {JSON.stringify(msg.data.state, null, 2)}
                  </pre>
                </div>
              )}
              {msg.data.metadata && (
                <div>
                  <span className="text-blue-300">Metadata:</span>
                  <pre className="text-xs bg-[#262626]/30 p-2 rounded mt-1 font-mono">
                    {JSON.stringify(msg.data.metadata, null, 2)}
                  </pre>
                </div>
              )}
            </div>
          </div>
        </div>
      );
    }

    if (msg.event === 'messages') {
      return (
        <div className="space-y-3">
          {msg.data.map((item: any, mIdx: number) => (
            <div key={mIdx} className="space-y-2">
              {item.type && (
                <div className="text-yellow-300/90 text-sm font-medium">
                  Type: {item.type}
                </div>
              )}
              {item.content && (
                <div className="text-gray-300 text-sm break-words pl-2">
                  Content: {item.content}
                </div>
              )}
              {mIdx === 1 && item && (
                <pre className="text-gray-400 text-xs font-mono bg-[#262626]/30 p-2 rounded mt-2">
                  {JSON.stringify(item, null, 2)}
                </pre>
              )}
            </div>
          ))}
        </div>
      );
    }

    if (msg.event === 'state_update' || msg.event === 'updates') {
      const state = msg.data.state || msg.data;
      return (
        <div className="space-y-3">
          <div className="space-y-2">
            <div className="text-yellow-300/90 text-sm font-medium">Thread State</div>
            
            {/* Checkpoint Info */}
            {state.checkpoint && (
              <div className="space-y-2">
                <div className="text-blue-300 text-sm">Checkpoint</div>
                <div className="pl-2 space-y-1 text-xs">
                  <div className="text-gray-300">
                    <span className="text-gray-500">ID:</span> {state.checkpoint.checkpoint_id}
                  </div>
                  <div className="text-gray-300">
                    <span className="text-gray-500">Created:</span> {new Date(state.checkpoint.created_at).toLocaleString()}
                  </div>
                  {state.checkpoint.parent_checkpoint_id && (
                    <div className="text-gray-300">
                      <span className="text-gray-500">Parent:</span> {state.checkpoint.parent_checkpoint_id}
                    </div>
                  )}
                </div>
              </div>
            )}

            {/* Values */}
            {state.values && (
              <div className="space-y-2">
                <div className="text-blue-300 text-sm">Values</div>
                <pre className="text-xs bg-[#262626]/30 p-2 rounded mt-1 font-mono overflow-x-auto">
                  {JSON.stringify(state.values, null, 2)}
                </pre>
              </div>
            )}

            {/* Tasks */}
            {state.tasks && (
              <div className="space-y-2">
                <div className="text-blue-300 text-sm">Tasks {state.tasks.length > 0 ? `(${state.tasks.length})` : ''}</div>
                {state.tasks.length > 0 ? (
                  <pre className="text-xs bg-[#262626]/30 p-2 rounded mt-1 font-mono overflow-x-auto">
                    {JSON.stringify(state.tasks, null, 2)}
                  </pre>
                ) : (
                  <div className="text-gray-500 text-xs pl-2">No active tasks</div>
                )}
              </div>
            )}

            {/* Next Steps */}
            {state.next && (
              <div className="space-y-2">
                <div className="text-blue-300 text-sm">Next Steps {state.next.length > 0 ? `(${state.next.length})` : ''}</div>
                {state.next.length > 0 ? (
                  <pre className="text-xs bg-[#262626]/30 p-2 rounded mt-1 font-mono overflow-x-auto">
                    {JSON.stringify(state.next, null, 2)}
                  </pre>
                ) : (
                  <div className="text-gray-500 text-xs pl-2">No pending steps</div>
                )}
              </div>
            )}

            {/* Metadata */}
            {state.metadata && (
              <div className="space-y-2">
                <div className="text-blue-300 text-sm">Metadata</div>
                <pre className="text-xs bg-[#262626]/30 p-2 rounded mt-1 font-mono overflow-x-auto">
                  {JSON.stringify(state.metadata, null, 2)}
                </pre>
              </div>
            )}

            {/* Raw State (Collapsed) */}
            <div className="mt-4 pt-4 border-t border-[#262626]/50">
              <details>
                <summary className="text-gray-400 text-xs cursor-pointer hover:text-gray-300">
                  Raw State
                </summary>
                <pre className="text-xs bg-[#262626]/30 p-2 rounded mt-2 font-mono overflow-x-auto">
                  {JSON.stringify(state, null, 2)}
                </pre>
              </details>
            </div>

            <div className="text-gray-400 text-xs mt-2">
              {new Date(msg.data.timestamp || new Date()).toLocaleString()}
            </div>
          </div>
        </div>
      );
    }

    return (
      <pre className="text-gray-300 text-sm whitespace-pre-wrap overflow-x-auto font-mono">
        {JSON.stringify(msg.data, null, 2)}
      </pre>
    );
  };

  return (
    <div className="h-full flex flex-col bg-[#141414] border-l border-[#262626]/50">
      <div className="flex-none p-4 border-b border-[#262626]/50 bg-[#1A1A1A]/50">
        <div className="flex items-center gap-2 text-gray-400">
          <Bug className="w-4 h-4" />
          <span className="text-sm font-medium">Debug Panel</span>
        </div>
      </div>

      <div className="flex-1 overflow-y-auto scrollbar-thin scrollbar-thumb-[#262626] scrollbar-track-transparent p-4 space-y-4">
        {consolidatedMessages.length === 0 ? (
          <div className="text-center text-gray-500 text-sm p-8">
            No messages to display
          </div>
        ) : (
          consolidatedMessages.map((msg, idx) => (
            <motion.div
              key={idx}
              variants={messageVariants}
              initial="hidden"
              animate="visible"
              exit="exit"
              className="group"
            >
              {msg.event === 'separator' ? (
                <div className="flex items-center gap-4 py-2 opacity-50">
                  <div className="h-px flex-1 bg-[#262626]" />
                  <div className="text-xs text-gray-500">New Interaction</div>
                  <div className="h-px flex-1 bg-[#262626]" />
                </div>
              ) : (
                <div className="rounded-lg overflow-hidden border border-[#262626]/50 bg-[#1A1A1A]/30">
                  <div className={cn(
                    "px-3 py-2 text-sm font-medium flex items-center gap-2",
                    msg.event === 'metadata' ? "bg-purple-500/10 text-purple-300" :
                    msg.event === 'stream_state' ? "bg-green-500/10 text-green-300" :
                    "bg-blue-500/10 text-blue-300"
                  )}>
                    <ChevronRight className="w-4 h-4" />
                    <span>event: {msg.event}</span>
                  </div>

                  <div className="p-3">
                    {renderEventData(msg)}
                  </div>
                </div>
              )}
            </motion.div>
          ))
        )}
        <div ref={messagesEndRef} />
      </div>
    </div>
  );
} 