'use client';

import { useState } from 'react';
import { Send } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';
import { motion } from 'framer-motion';

interface ChatInputProps {
  onSendMessage: (content: string) => Promise<void>;
  isLoading: boolean;
  isReady?: boolean;
}

export function ChatInput({ onSendMessage, isLoading, isReady = true }: ChatInputProps) {
  const [input, setInput] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim() || !isReady) return;

    try {
      await onSendMessage(input.trim());
      setInput('');
    } catch (error) {
      console.error('Error sending message:', error);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="max-w-3xl mx-auto p-4">
      <div className="relative flex items-center">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder={isReady ? "Message the assistant..." : "Select or create a chat to start"}
          className={cn(
            "w-full bg-white dark:bg-[#0A0A0A] text-gray-900 dark:text-gray-100 rounded-xl pl-4 pr-12 py-3.5",
            "border border-gray-200 dark:border-gray-800",
            "focus:outline-none focus:ring-1 focus:ring-amber-500/30 focus:border-amber-500/30",
            "placeholder:text-gray-400 dark:placeholder:text-gray-600",
            "shadow-sm dark:shadow-amber-500/5",
            "transition-all duration-200",
            (!isReady || isLoading) && "opacity-50 cursor-not-allowed"
          )}
          disabled={!isReady || isLoading}
        />
        <Button
          type="submit"
          size="icon"
          disabled={!input.trim() || !isReady || isLoading}
          className={cn(
            "absolute right-2 h-9 w-9",
            "bg-amber-500/10 hover:bg-amber-500/20 text-amber-600 dark:text-amber-500",
            "rounded-lg border-0",
            "transition-all duration-200",
            "disabled:opacity-0"
          )}
        >
          {isLoading ? (
            <motion.div
              animate={{ rotate: 360 }}
              transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
            >
              <Send className="h-4 w-4" />
            </motion.div>
          ) : (
            <Send className="h-4 w-4" />
          )}
        </Button>
      </div>
      {input.trim() && (
        <div className="absolute right-8 bottom-full mb-2">
          <kbd className="hidden sm:inline-block px-2 py-1 text-xs font-medium text-amber-500/40 bg-amber-500/5 rounded-md border border-amber-500/10">
            ⌘ ↵
          </kbd>
        </div>
      )}
    </form>
  );
} 