// a component that displays a message in the chat

'use client';

import { Message } from '@/types/chat';
import { Bot, User } from 'lucide-react';
import ReactMarkdown from 'react-markdown';

interface MessageItemProps {
  message: Message;
}

export function MessageItem({ message }: MessageItemProps) {
  return (
    <div className={`px-4 py-6 ${message.role === 'user' ? 'bg-transparent' : 'bg-[#f7f7f8] dark:bg-[#0A0A0A]'}`}>
      <div className="max-w-3xl mx-auto flex items-start gap-6">
        <div className="w-7 h-7 rounded-sm flex items-center justify-center flex-shrink-0">
          {message.role === 'user' ? (
            <User className="w-5 h-5 text-gray-500" />
          ) : (
            <Bot className="w-5 h-5 text-amber-500/90" />
          )}
        </div>
        <div className="flex-1 space-y-2">
          <div className="text-sm font-medium text-gray-500 dark:text-gray-400">
            {message.role === 'user' ? 'You' : 'Assistant'}
          </div>
          <ReactMarkdown 
            className="text-sm text-gray-800 dark:text-gray-200 leading-relaxed prose prose-pre:bg-[#1A1A1A] prose-pre:border prose-pre:border-gray-800/20 prose-pre:rounded-md dark:prose-invert"
          >
            {message.content}
          </ReactMarkdown>
        </div>
      </div>
    </div>
  );
} 