'use client';

import { MessageSquare, Plus, Trash2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';
import { useThread } from '@/contexts/ThreadContext';
import { useClient } from '@/contexts/ClientContext';
import { useState } from 'react';

interface Thread {
  thread_id: string;
  created_at: string;
  messages: Array<{
    role: string;
    content: string;
  }>;
}

interface ChatThreadProps {
  threads: Thread[];
  onNewChat: () => void;
  onSelectThread: (threadId: string) => void;
  activeThreadId?: string;
}

export function ChatThread({ threads, onNewChat, onSelectThread, activeThreadId }: ChatThreadProps) {
  const { deleteThread, deleteAllThreads } = useThread();
  const client = useClient();
  const [isCreating, setIsCreating] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);

  const handleNewChat = async () => {
    if (!client || isCreating) return;
    
    setIsCreating(true);
    try {
      // Let the parent handle thread creation
      onNewChat();
    } catch (error) {
      console.error('Error creating new thread:', error);
    } finally {
      setIsCreating(false);
    }
  };

  const handleDeleteThread = async (e: React.MouseEvent, threadId: string) => {
    e.stopPropagation();
    try {
      await deleteThread(threadId);
      // No need to do anything after deletion - ThreadContext will handle state updates
    } catch (error) {
      console.error('Error deleting thread:', error);
    }
  };

  const handleThreadSelect = (threadId: string) => {
    if (threadId === activeThreadId) return;
    onSelectThread(threadId);
  };

  const handleDeleteAll = async () => {
    if (!client || isDeleting) return;
    
    setIsDeleting(true);
    try {
      await deleteAllThreads();
    } catch (error) {
      console.error('Error deleting all threads:', error);
    } finally {
      setIsDeleting(false);
    }
  };

  return (
    <div className="w-[300px] h-full bg-[#0A0A0A] flex flex-col">
      <div className="p-4 space-y-3 border-b border-[#262626]/10">
        <Button 
          onClick={handleNewChat}
          className="w-full justify-start gap-2.5 bg-gradient-to-r from-amber-500 to-yellow-500 hover:from-amber-400 hover:to-yellow-400 text-black font-medium rounded-lg py-5 shadow-lg shadow-amber-500/20 transition-all duration-300"
          disabled={isCreating}
        >
          <Plus className="w-4 h-4" />
          {isCreating ? 'Creating...' : 'New Chat'}
        </Button>
        
        {threads.length > 0 && (
          <Button 
            onClick={handleDeleteAll}
            className="w-full justify-start gap-2.5 bg-transparent hover:bg-red-500/10 text-red-400 rounded-lg py-5 transition-all duration-300 border border-red-500/20"
            variant="ghost"
            disabled={isDeleting}
          >
            <Trash2 className="w-4 h-4" />
            {isDeleting ? 'Deleting...' : 'Clear All'}
          </Button>
        )}
      </div>

      <div className="flex-1 overflow-auto space-y-1 p-2">
        {threads.map((thread) => (
          <div
            key={`thread-${thread.thread_id}`}
            className={cn(
              "group relative w-full p-3 text-left transition-all duration-200 flex items-start gap-3 rounded-lg",
              "hover:bg-[#161616]",
              activeThreadId === thread.thread_id ? "bg-[#161616] shadow-sm" : "bg-transparent"
            )}
          >
            <button
              onClick={() => handleThreadSelect(thread.thread_id)}
              className="flex items-start gap-3 flex-1"
            >
              <div className={cn(
                "w-8 h-8 rounded-lg flex items-center justify-center flex-shrink-0 transition-colors duration-200",
                activeThreadId === thread.thread_id 
                  ? "bg-amber-500/20 text-amber-400" 
                  : "bg-[#1A1A1A] text-gray-400 group-hover:bg-[#202020]"
              )}>
                <MessageSquare className="w-4 h-4" />
              </div>
              <div className="flex-1 min-w-0">
                <p className="text-sm font-medium text-gray-200 truncate">
                  {new Date(thread.created_at).toLocaleString(undefined, {
                    month: 'short',
                    day: 'numeric',
                    hour: '2-digit',
                    minute: '2-digit'
                  })}
                </p>
                <p className="text-xs text-gray-500 truncate mt-1 font-light">
                  {thread.messages[thread.messages.length - 1]?.content || 'No messages yet'}
                </p>
              </div>
            </button>
            <button
              onClick={(e) => handleDeleteThread(e, thread.thread_id)}
              className="opacity-0 group-hover:opacity-100 transition-all duration-200 p-2 hover:bg-red-500/10 rounded-lg"
              disabled={isCreating}
            >
              <Trash2 className="w-3.5 h-3.5 text-gray-400 hover:text-red-400 transition-colors" />
            </button>
          </div>
        ))}

        {threads.length === 0 && (
          <div className="h-[200px] flex flex-col items-center justify-center text-center p-4">
            <div className="w-12 h-12 rounded-2xl bg-[#161616] flex items-center justify-center mb-4">
              <MessageSquare className="w-6 h-6 text-amber-500/70" />
            </div>
            <p className="text-sm text-gray-400 font-medium">No conversations yet</p>
            <p className="text-xs text-gray-600 mt-1">Start a new chat to begin</p>
          </div>
        )}
      </div>
    </div>
  );
} 